@import url('https://fonts.googleapis.com/css2?family=Fira+Mono:wght@400;500;700&family=Oswald:wght@200;300;400;500;700&family=Roboto+Slab:wght@100;200;300;400;500;600;700;800&family=Xanh+Mono&display=swap');
:root{
  /** fonts */
  --ft-1: 'Roboto Slab', serif;
  --ft-2: 'Oswald', sans-serif;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red-200: #d11e22;
  --red-100: #e90d0f;
  --alpha: rgba(0,0,0,.05 );
  --grys-300: #a2c2c8;
  --grys-200: #c9d8db;
  --grys-100: #fcffff;

  /** default theme */
  --theme-300: #f15e00;
  --theme-200: #f38400;
  --theme-100: #ebbc40;
  --second: #f29ea0;
  --dark-200: #1f2024;
  --dark-100: #25282d;
  --gradient-90: linear-gradient(90deg, var(--red-100), var(--red-200));
  --gradient-45: linear-gradient(45deg, var(--grys-100), var(--grys-300));
  --gradient: linear-gradient(var(--second), var(--theme-300));

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-3);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 92rem;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--dark-200) 20%, var(--dark-100) 20.05%, var(--dark-100) 80%, var(--dark-200) 80.05%);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--alpha);
  border-radius: .8rem
}

::-webkit-scrollbar-thumb{
  background: var(--grys-200);
  border-radius: .8rem;
}