/*890//750*/
.background_content{
      width: 100%;
      height: 100%;
      overflow: hidden;
      position: fixed;
      top: 0;
      left: 0;
      position: relative;
}

.bg_square{
      position: absolute;
      transform: rotate(45deg);
      top: 0;
      left: 0;
      z-index: -1;
}

.bg_square:nth-child(1){
      background: var(--theme-300);
      width: 35rem;
      height: 35rem;
      transform: translate(-30%, -50%) rotate(45deg);
}

.bg_square:nth-child(2){
      width: 42rem;
      height: 42rem;
      top: 100%;
      left: 100%;
      border: solid 10rem var(--theme-100);
      transform: translate(-60%, -100%) rotate(45deg);
}

.bg_square:nth-child(3){
      width: 20rem;
      height: 20rem;
      left: 90%;
      top: 5%;
      background: repeating-linear-gradient(var(--grys-100) 1px,var(--grys-100) 10px, transparent 11px, transparent 20px);
      transform: translate(-40%, 30%) rotate(45deg);
      -webkit-mask-image: radial-gradient(black 65%, transparent 65.6%);
      mask-image: radial-gradient(black 65%, transparent 65.6%);
}

.bg_circle{
      position: absolute;
      border-radius: 50%;
      z-index: -1;
      top: 0;
      left: 0;
}

.bg_circle:nth-child(4){
      width: 50rem;
      height: 50rem;
      top: 100%;
      background: var(--grys-200);
      transform: translate(-50%, -40%);
}

.bg_circle:nth-child(5){
      width: 50rem;
      height: 50rem;
      left: 100%;
      border: solid 5rem var(--theme-200);
      transform: translate(-50%, -50%);
}

.bg_circle:nth-child(6){
      width: 12rem;
      height: 12rem;
      top: 80%;
      left: 10%;
      border: solid .5rem var(--theme-100);
      transform: translate(-50%, 50%);
}