.content{
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 10;
      display: grid;
      place-content: center;
}

.content_inner{
      width: 89rem;
      height: 75rem;
      position: relative;
}

.wrapper_banner__user{
      width: 39rem;
      height: 61rem;
      border-radius: 3rem;
      position: absolute;
      left: 0;
      top: 8rem;
      background: var(--gradient-45);
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 10;
      box-shadow: 2.5rem 3rem 5rem rgba(0,0,0,.3);
}

.wrapper_banner__user:before,
.wrapper_banner__user:after{
      width: 25rem;
      height: 25rem;
      border-radius: 50%;
      background: var(--theme-100);
}

.wrapper_banner__user:before{
      top: 0;
      left: 0;
      transform: translate(-50%, -50%);
}

.wrapper_banner__user:after{
      bottom: 0;
      right: 0;
      transform: translate(50%, 50%);
      background: var(--theme-200);
}

.wrapper_banner__user img{
      width: Auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.container{
      position: relative;
      width: 77rem;
      height: 75rem;
      margin-left: 12rem;
      background: var(--grys-100);
      border-radius: 3rem;
      box-shadow: 0 0 7rem rgba(0,0,0,.5);
}

.banner_content{
      width: 100%;
      height: 22rem;
      border-radius: 3rem;
      background: var(--grys-100);
      position: relative;
      box-shadow: 0 .2rem .5rem rgba(0,0,0,.05);
      z-index: 2;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      border-radius: 3rem;
      background: var(--gradient-45);
}

.banner_content__img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, var(--grys-100));
      top: 0;
      left: 0;
      z-index: 1;
}

.banner_content__img img{
      filter: grayscale(1);
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      mix-blend-mode: multiply;
}

.banner_content__desc{
      width: 100%;
      height: 22rem;
      position: relative;
      padding: 2.5rem;
      z-index: 2;
}

.banner_toggle{
      width: fit-content;
      height: auto;
      position: absolute;
      top: 2.5rem;
      right: 2.5rem;
      display: flex;
      justify-content: flex-end;
}

.toggle_btn{
      width: 3rem;
      height: 4rem;
      background: none;
      position: relative;
}

.toggle_btn_bar{
      position: absolute;
      width: 3rem;
      height: .4rem;
      background: var(--light);
      top: calc(50% - .2rem);
      left: calc(50% - 1.5rem);
      transition: .2s;
}

.toggle_btn_bar:nth-child(1){
      transform: translateY(-1rem);
}

.toggle_btn_bar:nth-child(2){
      width: 1.5rem;
}

.toggle_btn_bar:nth-child(3){
      transform: translateY(1rem);
}

.toggle_btn_bar.active:nth-child(1),
.toggle_btn_bar.active:nth-child(2){
      width: 3rem;
      height: .15rem;
      transform: translateY(0) rotate(45deg);
}

.toggle_btn_bar.active:nth-child(3){
      height: .15rem;
      transform: translatey(0) rotate(-45deg);
}

.toggle_btn *{
      font-size: 3.5rem;
      color: var(--light);
}

.toggle_menu{
      position: absolute;
      top: 0;
      right: 4rem;
      padding: 1rem;
      background: var(--theme-200);
      border-radius: .3rem;
      box-shadow: .5rem 1rem 2.5rem rgba(0,0,0,.5);
      transform: scale(0);
      transform-origin: right top;
      display: flex;
      flex-direction: column;
      gap: .2rem;
      z-index: 10;
}

.toggle_menu.active{
      animation: show_menu .3s ease-in-out forwards;
}

@keyframes show_menu{
      0%{
            transform: scale(0);
      }
      60%{
            transform: scale(1.4);
      }
      100%{
            transform: scale(1);
      }
}

.toggle_menu a{
      width: 8.5rem;
      height: 2.6rem;
      border-radius: .3rem;
      background: var(--theme-100);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      color: var(--light);
      transition: .2s;
}

.toggle_menu a:hover{
      background: var(--dark-100);
}

.banner_user{
      width: fit-content;
      height: 12rem;
      position: absolute;
      top: 8rem;
      left: 30rem;
      display: flex;
      gap: 1rem;
}

.user_avatar_img{
      width: 10rem;
      height: 10rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--theme-200) inset;
      padding: .5rem;
}

.user_avatar_img img{
      width: 9rem;
      height: 9rem;
      border-radius: 50%;
}

.user_data h1{
      text-transform: uppercase;
      font-size: 2.2rem;
}

.user_data strong{
      margin-left: auto;
      display: block;
      text-align: right;
      font-size: 2.2rem;
      color: var(--theme-100);
      line-height: 1.8rem;
}

.user_data p{
      font-size: 1.4rem;
}

.user_data_location{
      display: flex;
      align-items: center;
      gap: .5rem;
      margin-top: 1rem;
}

.user_data_location *{
      font-size: 3rem;
      color: var(--theme-200);
}

.user_data_location span{
      font-size: 1.4rem;
      color: var(--light);
      background: var(--theme-200);
      padding: .4rem 1.5rem;
      border-radius: 999px;
}

.wrapper_pages{
      width: 50rem;
      height: 53rem;
      margin-left: 27rem;
      position: relative;
      z-index: 1;
}

.pages_group,
.pages_content{
      width: 36rem;
      height: 53rem;
      background: var(--grys-100);
      position: relative;
}

.pages_group:before{
      width: 3rem;
      height: 75%;
      background: #000;
      border-radius: 50%;
      right: -1.5rem;
      top: 12.5%;
      z-index: 1;
      filter: blur(2.5rem);
      opacity: .3;
}

.pages_content{
      z-index: 2;
}

.pages_menu{
      width: 14rem;
      height: 53rem;
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      align-items: center;
}

.pages_menu ul{
      display: flex;
      flex-direction: column;
      gap: 1rem;
}

.menu_item{
      width: 12.5rem;
      height: 3.8rem;
      background: transparent;
      padding-left: 2rem;
      position: relative;
      display: flex;
      align-items: center;
      gap: 1rem;
      border-radius: 0 999px 999px 0;
      transition: .2s;
      cursor: pointer;
      transform: translateX(-1rem);
}

.menu_item *{
      color: var(--theme-300);
      font-size: 2rem;
}

.menu_item span{
      text-transform: uppercase;
      font-size: 1.4rem;
}

.menu_item:hover,
.menu_item.active{
      background: var(--theme-300);
}

.menu_item:hover *,
.menu_item.active *{
      color: var(--light);
}