.btn_social{
      width: 3.8rem;
      height: 3.8rem;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.05);
}

.btn_social *{
      font-size: 1.6rem;
      color: var(--theme-200);
}

.btn_social:hover{
      background: var(--theme-200);
}

.btn_social:hover *{
      color: var(--light);
}