.wrapper_online{
      width: fit-content;
      height: 3.8rem;
      display: flex;
      gap: .5rem;
}

.online_avatar,
.online_avatar img{
      width: 3.8rem;
      height: 3.8rem;
      border-radius: 50%;
      display: flex;
}

.online_avatar{
      position: relative;
}

.online_dots{
      width: 1.2rem;
      height: 1.2rem;
      position: absolute;
      top: 0;
      right: 0;
      background: var(--light);
      border-radius: 50%;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-100);
}

.wrapper_online span{
      width: fit-content;
      height: fit-content;
      padding: .4rem 1.5rem;
      border-radius: 999px;
      background: var(--theme-200);
      text-transform: uppercase;
}