.empty_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      gap: 2rem;
}

.empty_icon *{
      color: var(--alpha);
      font-size: 8rem;
      margin-top: 5rem;
}

.empty_content h3{
      color: var(--grys-200);
      font-size: 1.8rem;
}