.content_page{
      background: var(--grys-100);
      width: 100%;
      height: 100%;
      padding: 5rem 2.5rem;
      position: relative;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
}

.content_page.show{
      animation: show_page .5s ease-in-out forwards;
}

@keyframes show_page{
      100%{
            transform: translateX(0);
            opacity: 1;
            visibility: visible;
      }
}

.content_page h1{
      color: var(--theme-100);
      font-size: 1.8rem;
}

.home_description{
      margin-top: 2rem;
      width: 100%;
      height: auto;
      max-height: 20rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--grys-200);
}

.home_description p{
      font-size: 1.4rem;
      color: var(--grys-300);
}

.wrapper_social{
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 4rem;
}

.wrapper_social h3{
      text-transform: uppercase;
      color: var(--grys-300);
      font-size: 1.2rem;
      font-weight: 500;
      letter-spacing: .2rem;
}

.social_menu{
      margin-top: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.content_page__inner{
      width: 100%;
      height: 100%;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--grys-200);
      display: flex;
      flex-direction: column;
      align-items: center;
}

.relation_avatar{
      width: 14rem;
      height: 14rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--theme-200) inset;
      padding: .5rem;
}

.relation_avatar img{
      width: 13rem;
      height: 13rem;
      border-radius: 50%;
}

.content_page__inner h2{
      color: var(--theme-200);
      text-transform: uppercase;
      font-size: 2rem;
      position: relative;
      margin-top: 4rem;
}

.content_page__inner h2:before{
      content: attr(data-id);
      color: var(--light);
      background: var(--theme-200);
      padding: .4rem 1.5rem;
      border-radius: 999px;
      font-size: 1rem;
      top: -2rem;
      left: 50%;
}

.relation_description{
      margin: 2rem 0;
      width: 100%;
      height: auto;
}

.relation_description p{
      color: var(--grys-300);
      font-size: 1.4rem;
}

.content_page__inner a{
      padding: .4rem 1.5rem;
      text-transform: uppercase;
      background: var(--theme-200);
      border-radius: 999px;
      color: var(--light);
      transition: .2s;
      position: relative;
      margin-left: 100%;
      width: fit-content;
      transform: translateX(-100%);
}

.content_page__inner a:hover{
      background: var(--theme-300);
}

.friend_content{
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
      gap: 3rem;
      padding: 2rem;
}

.friend_row{
      width: 26rem;
      height: 7rem;
      background: var(--light);
      border-radius: .4rem;
      box-shadow: 1.5rem 1.5rem 1.5rem rgba(0,0,0,.1);
      display: flex;
      gap: 1rem;
}

.friend_row__avatar,
.friend_row__avatar img{
      width: 7rem;
      height: 7rem;
      border-radius: .4rem;
      display: flex;
}

.friend_row__avatar{
      position: relative;
      transform: translate(-1.5rem, -1.3rem);
      box-shadow: 1rem 1rem 1.5rem rgba(0,0,0,.2);
      flex-shrink: 0;
}

.friend_row__data{
      flex: 1;
      padding: 1rem 1rem 1rem 0;
      position: relative;
}

.friend_row__data strong{
      color: var(--theme-200);
      font-size: 1.4rem;
}

.friend_row__data p{
      color: var(--grys-300);
      margin-top: .5rem;
}

.friend_row__data a{
      position: absolute;
      right: 0;
      bottom: 0;
      transform: translate(-2rem, 50%);
}

.content_page__inner.video{
      justify-content: flex-start;
      z-index: 2;
      position: relative;
}

.video_title{
      width: 100%;
      text-align: center;
      color: var(--grys-300);
      font-size: 1.8rem;
      font-weight: 500;
}

.video_box{
      width: 30rem;
      height: 17rem;
      background: #222;
      margin-top: 2rem;
      position: relative;
}

.video_box:before{
      width: 70%;
      height: 4rem;
      border-radius: 50%;
      background: #000;
      left: 15%;
      bottom: -1rem;
      filter: blur(2rem);
      z-index: -1;
      opacity: .5;
}

.video_box iframe{
      border: none;
}